import { useState } from "react";
import styles from "../Styles/Footer.module.css";
import FooterWindow from "./FooterWindow";
import imgGaliscope from "../Assets/img/footer/clients/galiscopeblanco.png";
import imgConcasa from "../Assets/img/footer/clients/concasablanco.png";
import imgHbaArchitects from "../Assets/img/footer/clients/hba.png";
import imgJosephKasbar from "../Assets/img/footer/clients/jk.png";
import imgJTCArchitects from "../Assets/img/footer/clients/jtc.png";
import imgMRArchitecture from "../Assets/img/footer/clients/mrarchitecturedt.png";
import imgGrupoJCarter from "../Assets/img/footer/clients/jcarterblanco.png";

import imgLinkedIn from "../Assets/img/footer/networks/linkedinblanco.png";
import imgFacebook from "../Assets/img/footer/networks/facebookblanco.png";
import imgInstagram from "../Assets/img/footer/networks/instagramblanco.png";
import imgYouTube from "../Assets/img/footer/networks/youtubeblanco.png";
import imgBehance from "../Assets/img/footer/networks/behanceblanco.png";

import imgVisa from "../Assets/img/footer/paymethods/visagris.png";
import imgPaypal from "../Assets/img/footer/paymethods/paypalgris.png";
import imgMC from "../Assets/img/footer/paymethods/mastercardgris.png";
import imgBank from "../Assets/img/footer/paymethods/girobancariogris.png";

interface itemsInfoI
  extends Array<{
    position: number;
    title: string;
    url: string;
    img: string;
  }> {}

const clients: itemsInfoI = [
  {
    position: 1,
    title: "Galiscope",
    url: "https://www.galiscope.com/",
    img: imgGaliscope,
  },
  {
    position: 2,
    title: "Concasa",
    url: "https://concasa.com/",
    img: imgConcasa,
  },
  {
    position: 3,
    title: "Grupo J.Carter",
    url: "https://www.grupojcarter.com/",
    img: imgGrupoJCarter,
  },
  {
    position: 4,
    title: "Hba Architects",
    url: "https://hbaarchitects.com/about-us/",
    img: imgHbaArchitects,
  },
  {
    position: 5,
    title: "Joseph Kasbar",
    url: "https://www.josephkasbar.com/",
    img: imgJosephKasbar,
  },
  {
    position: 6,
    title: "JTC Architects",
    url: "https://jtcarch.com",
    img: imgJTCArchitects,
  },
  {
    position: 7,
    title: "MR Architecture dt",
    url: "https://mrarchitecturedt.ch",
    img: imgMRArchitecture,
  },
];
const networks: itemsInfoI = [
  {
    position: 1,
    title: "LinkedIn",
    url: "https://ar.linkedin.com/company/uizarq",
    img: imgLinkedIn,
  },
  {
    position: 2,
    title: "Facebook",
    url: "https://es-la.facebook.com/uizarq/",
    img: imgFacebook,
  },
  {
    position: 3,
    title: "Instagram",
    url: "https://www.instagram.com/uizarq/?hl=es",
    img: imgInstagram,
  },
  {
    position: 4,
    title: "YouTube",
    url: "https://www.youtube.com/channel/UCgsVP6qNnE0wVeI8xViwRIA",
    img: imgYouTube,
  },
  {
    position: 5,
    title: "Behance",
    url: "https://www.behance.net/uizarq",
    img: imgBehance,
  },
];

export default function Footer() {
  const [stateWindow, setWindow] = useState<0 | 1 | 2>(0);

  function RenderItemsLoop({ itemsInfo }: { itemsInfo: itemsInfoI }) {
    const items = [];
    for (let index = 1; index <= itemsInfo.length; index++) {
      const item = itemsInfo.find((element) => element.position === index);
      items.push(
        <a href={item!.url} target="_blank" rel="noreferrer" key={index}>
          <img draggable="false" src={item!.img} alt={item!.title} />
        </a>
      );
    }
    return <>{items}</>;
  }
  return (
    <div className={styles.container}>
      <span className={styles.rainbowLine} />
      <div className={styles.clientsContainer}>
        <h4>SOME CLIENTS</h4>
        <div className={styles.clients}>
          {RenderItemsLoop({ itemsInfo: clients })}
        </div>
      </div>
      <span className={styles.grayLine} />
      <div className={styles.networks}>
        {RenderItemsLoop({ itemsInfo: networks })}
      </div>
      <span className={styles.grayLine} />
      <div className={styles.foot}>
        <div className={styles.legalInfo}>
          <img
            src={require("../Assets/img/logo.webp")}
            alt="Logo Uizarq"
            draggable="false"
          />
          |<button onClick={() => setWindow(1)}>LEGAL</button>|
          <button onClick={() => setWindow(2)}>PRIVACY</button>
        </div>
        <div className={styles.payMethods}>
          <img alt="VISA" src={imgVisa} />
          <img alt="PayPal" src={imgPaypal} />
          <img alt="MasterCard" src={imgMC} />
          <img alt="Banco" src={imgBank} />
        </div>
        <p className={styles.contactInfo}>
          UIZARQ LLC.
          <span className={styles.whitespace} />
          <span className={styles.whitespace} />
          30 N Gould St STE N
          <span className={styles.whitespace} />
          Sheridan, WY 82801
          <span className={styles.whitespace} />
          <span className={styles.whitespace} />
          Phone:{" "}
          <a
            href="tel:+5493644354975"
            style={{ textDecoration: "none", color: "gray" }}
          >
            +54 9 364 435-4975
          </a>
          <span className={styles.whitespace} />
          E-mail:
          <a
            href="mailto:info@uizarq.com"
            style={{ textDecoration: "none", color: "gray" }}
          >
            info@uizarq.com
          </a>
        </p>
      </div>
      <FooterWindow stateWindow={stateWindow} setWindow={setWindow} />
    </div>
  );
}
