import { useCallback, useEffect, useRef, useState } from "react";
import { NavLink as GenericNavLink, useLocation } from "react-router-dom";
import styles from "../Styles/Header.module.css";
import LangSwitch from "./LangSwitch";

const NavLink = ({ ...props }) => {
  return (
    <GenericNavLink
      to={props.to}
      {...props}
      style={({ isActive }) => ({
        color: isActive ? "rgb(75, 75, 75)" : "white",
        borderBottom: "1px solid " + (isActive ? "rgb(75, 75, 75)" : "white"),
      })}
    >
      {props.children}
    </GenericNavLink>
  );
};

export default function Header() {
  const path = useLocation().pathname;

  const HeaderRef = useRef<HTMLHeadElement>(null);
  const [stateMenu, setMenuState] = useState(false);

  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    function HeaderListener() {
      if (
        document.documentElement.scrollTop >=
        document.documentElement.clientHeight -
          document.documentElement.clientHeight / 2
      ) {
        HeaderRef.current!.classList.add(styles.headerActive);
      } else {
        HeaderRef.current!.classList.remove(styles.headerActive);
      }
    }
    if (path === "/") {
      HeaderRef.current!.classList.remove(styles.headerActive);
      window.addEventListener("scroll", HeaderListener);
      return () => {
        window.removeEventListener("scroll", HeaderListener);
      };
    } else {
      HeaderRef.current!.classList.add(styles.headerActive);
    }
  }, [path]);

  return (
    <header ref={HeaderRef} className={styles.header}>
      <LangSwitch error={false} />
      <a href="/" className={styles.logo}>
        <img src={require("../Assets/img/logo.webp")} alt="" />
      </a>
      <button
        className={styles.button + (stateMenu ? " " + styles.buttonActive : "")}
        onClick={() => setMenuState(!stateMenu)}
      />
      <nav className={styles.nav + (stateMenu ? " " + styles.navActive : "")}>
        <ul>
          <li>
            <NavLink to="/">Projects</NavLink>
          </li>
          <li>
            <NavLink to="/Prices">Render Quote</NavLink>
          </li>
          <li>
            <NavLink to="/AboutUs">About Us</NavLink>
          </li>
          <li>
            <NavLink to="/Contact">Contact</NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
}
