interface galleryItemsI
  extends Array<{
    row: number;
    items:
      | {
          position: number;
          type: "img";
          name: string;
          gridColumns: number;
        }[]
      | {
          position: number;
          type: "slider";
          name: { 1: string; 2: string };
          gridColumns: number;
        }[]
      | {
          position: number;
          type: "iframe";
          src: string;
          gridColumns: number;
        }[]
      | {
          position: number;
          type: "video";
          src: string;
          gridColumns: number;
        }[];
    height: {
      "+960": string;
      "-960": string;
    };
  }> {}

function RequireImg(img: string) {
  return require("../Assets/img/" + img);
}
