function MouseDown(e: any) {
  let targetParentNode = e.target.parentNode.parentNode;
  function MouseMove(e: any) {
    targetParentNode.style.width =
      e.clientX - targetParentNode.getBoundingClientRect().x + "px";
  }
  function MouseUp() {
    window.removeEventListener("mousemove", MouseMove);
    window.removeEventListener("mouseup", MouseUp);
  }
  window.addEventListener("mousemove", MouseMove);
  window.addEventListener("mouseup", MouseUp);
}
function TouchStart(e: any) {
  let targetParentNode = e.target.parentNode;
  function touchMove(e: any) {
    targetParentNode.style.width =
      e.changedTouches[0].clientX -
      targetParentNode.getBoundingClientRect().x +
      "px";
  }
  function touchEnd() {
    window.removeEventListener("touchmove", touchMove);
    window.removeEventListener("touchend", touchEnd);
  }
  window.addEventListener("touchmove", touchMove);
  window.addEventListener("touchend", touchEnd);
}

export default function Home() {
  // ??? error
  const [stateMediaMenos960, setMediaMenos960] = useState({
    matches: window.innerWidth > 960,
  });
  const Gallery = useCallback(() => {
    const items = [];
    for (let index = 1; index <= galleryItems.length; index++) {
      const item = galleryItems.find((element) => element.row === index);
      const elementsOfItem: JSX.Element[] = [];
      item?.items.forEach((element, i) => {
        switch (element.type) {
          case "img":
            elementsOfItem.push(
              <a
                key={i}
                href={RequireImg("home/gallery/" + element.name)}
                style={{
                  gridColumn: "span " + element.gridColumns,
                  height: stateMediaMenos960.matches
                    ? item.height["+960"]
                    : item.height["-960"],
                }}
              >
                <img
                  src={RequireImg("home/gallery/" + element.name)}
                  alt=""
                  style={{
                    height: stateMediaMenos960.matches
                      ? item.height["+960"]
                      : item.height["-960"],
                  }}
                />
              </a>
            );
            break;
          case "slider":
            elementsOfItem.push(
              <div
                key={i}
                className={styles.sliderGalleryContainer}
                style={{
                  gridColumn: "span " + element.gridColumns,
                  height: stateMediaMenos960.matches
                    ? item.height["+960"]
                    : item.height["-960"],
                }}
              >
                <div
                  className={styles.draggableSliderContainer}
                  style={{
                    gridColumn: "span " + element.gridColumns,
                    maxWidth: `calc(${
                      (100 / 18) * element.gridColumns
                    }vw - 10px)`,
                  }}
                >
                  <div
                    style={{
                      overflow: "hidden",
                      height: stateMediaMenos960.matches
                        ? item.height["+960"]
                        : item.height["-960"],
                    }}
                  >
                    <img
                      draggable="false"
                      style={{
                        width: `calc(${
                          (100 / 18) * element.gridColumns
                        }vw - 10px)`,
                      }}
                      src={RequireImg("home/gallery/" + element.name[1])}
                      alt=""
                    />
                  </div>
                  <button onMouseDown={MouseDown} onTouchStart={TouchStart}>
                    <img
                      draggable="false"
                      className={styles.draggableSliderBttn}
                      style={{
                        width: "50px",
                        height: "50px",
                      }}
                      src={RequireImg("home/gallery/buttonSlider.svg")}
                      alt=""
                    />
                  </button>
                </div>
                <img
                  draggable="false"
                  src={RequireImg("home/gallery/" + element.name[2])}
                  style={{
                    gridColumn: "span " + element.gridColumns,
                    height: stateMediaMenos960.matches
                      ? item.height["+960"]
                      : item.height["-960"],
                  }}
                  alt=""
                />
              </div>
            );
            break;
          case "iframe":
            elementsOfItem.push(
              <iframe
                key={i}
                frameBorder="0"
                allowFullScreen
                title="Uizarq embed iframe"
                scrolling="no"
                src={element.src}
                style={{
                  gridColumn: "span " + element.gridColumns,
                  width: "100%",
                  height: stateMediaMenos960.matches
                    ? item.height["+960"]
                    : item.height["-960"],
                }}
              />
            );
            break;
          case "video":
            elementsOfItem.push(
              <video
                key={i}
                muted
                loop
                autoPlay
                style={{
                  gridColumn: "span " + element.gridColumns,
                  width: "100%",
                  height: stateMediaMenos960.matches
                    ? item.height["+960"]
                    : item.height["-960"],
                  objectFit: "cover",
                }}
              >
                <source
                  src={require("../Assets/video/gallery/" + element.src)}
                />
              </video>
            );
            break;

          default:
            break;
        }
      });
      items.push(elementsOfItem);
    }
    return <div className={styles.gallery}>{items}</div>;
  }, [stateMediaMenos960]);

  useEffect(() => {
    const MediaListener = window.matchMedia("(min-width: 960px)");
    MediaListener.addEventListener("change", setMediaMenos960);
    baggueteBox.run("." + styles.gallery);
    return () => MediaListener.removeEventListener("change", setMediaMenos960);
  }, []);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.presentacion}>
          <div className={styles.bgSlider}>
            <img src={RequireImg("home/bg.webp")} alt="backgroundSlide" />
            <img src={RequireImg("home/bg.webp")} alt="backgroundSlide" />
          </div>
          <main className={styles.mainPresentacion}>
            <img
              src={RequireImg("logo.webp")}
              draggable="false"
              height="38"
              width="230"
              alt="Logo"
            />
            <p>
              We seek to provide a service of high technical and aesthetic level
              for companies dedicated to architecture. Help them communicate
              each project efficiently and increase their client portfolio.
            </p>
            <a
              className={styles.bttnGeneric}
              href="https://cutt.ly/LNDWKt8"
              target="_blank"
              rel="noopener noreferrer"
            >
              Download Portfolio
            </a>
          </main>
        </div>
        <Gallery />
        <Link
          className={styles.bttnGeneric + " " + styles.renderq}
          to="/Prices"
        >
          Render Quote
        </Link>
      </div>
    </>
  );
}
