import { useContext } from "react";
// import { LoadedContext } from "../App";

function Onload(
  setPageLoadedState: React.Dispatch<React.SetStateAction<boolean>>
) {
  const imgs = Array.from(document.images);
  const videos = Array.from(document.getElementsByTagName("video"));

  const ImgsLoaded = imgs.filter((img) => img.complete);
  const VideosLoaded = videos.filter((video) => video.readyState >= 3);

  if (
    ImgsLoaded.length === imgs.length &&
    VideosLoaded.length === videos.length
  ) {
    setPageLoadedState(true);
  }
}

export function Image(
  props: React.DetailedHTMLProps<
    React.ImgHTMLAttributes<HTMLImageElement>,
    HTMLImageElement
  >
) {
  // const { setPageLoadedState } = useContext(LoadedContext)!;
  return (
    <img
      {...props}
      alt={props.alt}
      onLoad={(e) => {
        // Onload(setPageLoadedState);
        props.onLoad?.(e);
      }}
    >
      {props.children}
    </img>
  );
}

export function Video(
  props: React.DetailedHTMLProps<
    React.VideoHTMLAttributes<HTMLVideoElement>,
    HTMLVideoElement
  >
) {
  // const { setPageLoadedState } = useContext(LoadedContext)!;
  return (
    <video
      {...props}
      onLoadedData={(e) => {
        // Onload(setPageLoadedState);
        props.onLoadedData?.(e);
      }}
    >
      {props.children}
    </video>
  );
}
